import tw from "tailwind-styled-components";
import "./App.css";
import Header from "./components/Header";
import Player from "./components/Player";
import { useEffect, useState } from "react";
import Playlist from "./components/Playlist";
import { refreshItems } from "./utils/localstorage";

export interface IYoutube {
  id: string;
  title: string;
  originalTitle: string;
  videoId: string;
}

function App() {
  const [onPlayer, setOnPlayer] = useState<boolean>(false);
  const [youtubes, setYoutubes] = useState<IYoutube[]>([
    {
      id: "1",
      title: "",
      originalTitle:
        ",    |   OST ( )",
      videoId: "L5No7rhWM-I",
    },
    {
      id: "2",
      title: "",
      originalTitle: "      +*",
      videoId: "2YLs33C72zo",
    },
    {
      id: "3",
      title: "",
      originalTitle:
        "      . , , , ,  ,  ,  ",
      videoId: "x6i3_LfeTjY",
    },
    {
      id: "4",
      title: " ",
      originalTitle:
        "[playlist]      | , , , , , ",
      videoId: "75kySTFaBQQ",
    },
    {
      id: "5",
      title: " ",
      originalTitle:
        " 2030        | PLAYLIST",
      videoId: "7r-f4QK50NA",
    },
    {
      id: "6",
      title: "",
      originalTitle:
        "      J-POP    ",
      videoId: "cyY8pL6e_b0",
    },
    {
      id: "7",
      title: " ost",
      originalTitle:
        "[Playlist] 1        !  ost ",
      videoId: "gIIzk2B7ruw",
    },
  ]);
  const [playItem, setPlayItem] = useState<IYoutube | null>(null);

  useEffect(() => {
    const refreshBookmarkItems = refreshItems("youtubes", setYoutubes);
    refreshBookmarkItems();
    window.addEventListener("storage", refreshBookmarkItems);
    return () => {
      window.removeEventListener("storage", refreshBookmarkItems);
    };
  }, []);

  return (
    <Container>
      <Header />
      <Content>
        {!onPlayer && (
          <Playlist
            setOnPlayer={setOnPlayer}
            setYoutubes={setYoutubes}
            youtubes={youtubes}
            setPlayItem={setPlayItem}
            playItem={playItem}
          />
        )}
        {onPlayer && playItem && (
          <Player setOnPlayer={setOnPlayer} playItem={playItem} />
        )}
      </Content>
    </Container>
  );
}

const Container = tw.div`
w-screen
m-0
flex
flex-col
items-center
`;

const Content = tw.div`
w-[28rem]
`;

export default App;
